body{
  margin: 0;
  padding: 0;
  background: url(../img/ibg.jpg) center center;
  width: 100%;
  max-width: 100%;
  overflow-x: hidden;
  min-height: 100vh;
  flex-direction: column;
  font-family:'Montserrat';
  font-weight: 400;
  background-color: #fff;
}

@mixin Open($size){
    font-family: 'Open Sans';
    font-weight: $size;
}

#header{
  position: fixed;
  width: 100vw;
  height: fit-content;
  padding: 30px 50px;
  left: 0;
  top: 0;
}

.header_logo{
  transition: .3s;
  display: inline-block;
  -webkit-transition: 0.2s;
  transition: 0.2s;
  width: 200px;

  img{
    width: 100%;
    height: 100%;
  }
  
  &:hover{
    -webkit-transform: scale(1.1);
    transform: scale(1.1);
  }
}

.wrapper{
  margin: 0 auto;
  margin-top: 20px;
  position: relative;
  display: flex;
  flex-direction: column;
  max-width: 1140px;
}

.intro-text{
  text-align: center;
  color: #fff;
  font-size: 28px;
  font-weight: 700;
  width: 100%;
  max-width: 740px;
  margin: 0 auto;
  line-height: 40px;
  margin-bottom: 10px;
}

.main-video{
  width: 100%;
  position: relative;
  border: 3px solid #fff;
  background-color: #000;
  margin: 1rem auto;
  max-width: 975px;
  height: 551px;

  video{
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border: 0;
  }
}
